

import { createClient } from "@supabase/supabase-js";
import { conditional_import } from "../ppm/util/conditional_import";


export const SUPABASE_ID = "yvbanllkrinkckegmuua";
export const SUPABASE_EDGE_BASE_URL = `https://${SUPABASE_ID}.functions.supabase.co/`
export const SUPABASE_URL = `https://${SUPABASE_ID}.supabase.co`;
export const SUPABASE_ANON_KEY : any = await conditional_import(
  "../ppm.config",
  "supabase",
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Inl2YmFubGxrcmlua2NrZWdtdXVhIiwicm9sZSI6ImFub24iLCJpYXQiOjE3NzAyNDg5NzksImV4cCI6MjA4NTgyNDk3OX0.pgOOt2Hcgiv1oVOlwJR1jtG-v71sC31CAm2W7GgdlV0"
);

export const supabase = createClient(SUPABASE_URL, SUPABASE_ANON_KEY, {
  auth: {
    persistSession: true,
    detectSessionInUrl: true,
    autoRefreshToken: true,

    //  REMOVE storageKey completely
    // storageKey: SUPABASE_STORAGE_KEY,

    storage:
      typeof window !== "undefined" ? window.localStorage : undefined,
  },

  global: {
    fetch: async (url, options: any = {}) => {
      //  Skip Supabase Auth endpoints
      if (
        typeof url === "string" &&
        (url.includes("/auth/v1/token") ||
          url.includes("/auth/v1/signup") ||
          url.includes("/auth/v1/logout"))
      ) {
        return fetch(url, options);
      }

      try {
        const { data } = await supabase.auth.getSession();
        const token = data?.session?.access_token;
        if (token) {
          options.headers = {
            ...(options.headers || {}),
            Authorization: `Bearer ${token}`,
          };
        }
      } catch (e) {
        console.warn(" Could not attach auth header:", e);
      }

      options.headers = {
        "Content-Type": "application/json",
        ...(options.headers || {}),
      };

      return fetch(url, options);
    },
  },
});


