let _ppmCache: any | null = null;

export async function conditional_import<T>(
  modulePath: string,
  key: string,
  fallback: T
): Promise<T> {

  if (_ppmCache === null) {
    try {
      const mod = await import(modulePath); 
      _ppmCache = mod.PPM_CONFIG ?? {};
    } catch (err) {
      console.warn("[conditional_import] failed to load", modulePath, err);
      _ppmCache = {};
    }
  }

  return _ppmCache?.[key] ?? fallback;
}
